import createCard from './createCard'
import './gallery.css'
import cometlist from './cometlist'

const Comets = () => {
    return (
        <main>
        <p className="category_desc">Комета — небольшое небесное тело, обращающееся вокруг Солнца по весьма вытянутой орбите. По мере приближения к Солнцу, водяные, метановые и другие газы, входящие в состав комет, испаряются, образуя кому и иногда газопылевой хвост.</p>
        <div className='gallery_grid'>
            {cometlist.map((cometImage) => (
                createCard(cometImage)
            ))}
        </div>
        </main>
    );
}

export default Comets